@import '../../../assets/less/mixins';

.AudioBlock {
    border: solid thin #d8d8d8;
    padding: 10px;
}
    .cw-audio-description {
        padding-bottom: 1em;
    }
    
    .cw-audio-controls {
        text-align: right;
    }
    
    .cw-audio-range {
        margin: 0 5px 10px 0;
        &.ui-widget-content {
            background-color: #24437C;
        }
        
        .ui-widget-header {
            background-color: #fdfdfd;
        }

        .ui-slider-handle {
            border-radius: 20px;
            width: 1.8em;
            height: 1.8em;
            top: -0.5em;
            background-color: #ddd;
            border-color: #ccc;
            cursor: pointer;
            margin-left: -3px;
        }
    }

    .cw-audio-playbutton, .cw-audio-stopbutton {
        .courseware-btn;
        margin: 5px;
        height: 46px;
        width: 46px;
        background-position: 6px center;
        background-color: #f3f3f3;
        display: inline-block;
    }

    .cw-audio-playbutton {
        .assets-bg-icon("blue/play.svg");
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 28px;
    }

    .cw-audio-stopbutton {
        .assets-bg-icon("blue/stop.svg");
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 24px;
    }

    .cw-audio-playbutton:hover,
    .cw-audio-stopbutton:hover{
        background-color: #ddd;
    }

    .cw-audio-playbutton-playing {
        .assets-bg-icon("blue/pause.svg");
        background-position: center center;
        background-repeat: no-repeat;
        background-size: 24px;
    }

    .cw-audio-time {
        position: relative;
        top: 0.5em;
        color: #333;
    }






