@import '../../../assets/less/mixins';

.DownloadBlock {
    border: solid thin #d8d8d8;
    padding: 10px;
}
.cw-download-info { 
    .assets-bg-icon("blue/file.svg");
    background-repeat: no-repeat;
    background-size: 24px;
    padding-left: 26px;
    line-height: 24px; 
    margin-top: 4px;
    span {
        color: lightgrey;
    }
}
.cw-download-button-wrapper {
    text-align: right;
    margin-top: -2em;
    .cw-download-button {
    .courseware-btn;
        background-position: 16px center;
        font-size: 14px;
        padding-left: 48px;
        padding-right: 20px;;
        background-color: #eee;
        display: inline-block;
        .assets-bg-icon("blue/download.svg");
        background-repeat: no-repeat;
        background-size: 20px;
    }
}





